export const sourceCode = "---\r\ntitle: \"Correct Sentence: 'The man who said that was a fool'\"\r\ndescription: \"The man who said that was a fool - ‡¶ï‡ßá‡¶® ‡¶è‡¶á ‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø‡¶ü‡¶ø ‡¶∂‡ßÅ‡¶¶‡ßç‡¶ß? Relative Pronoun 'who' ‡¶è‡¶¨‡¶Ç 'whom' ‡¶è‡¶∞ ‡¶∏‡¶†‡¶ø‡¶ï ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞ ‡¶∂‡¶ø‡¶ñ‡ßÅ‡¶®‡•§ BCS, Primary ‡¶™‡¶∞‡ßÄ‡¶ï‡ßç‡¶∑‡¶æ‡¶∞ ‡¶ú‡¶®‡ßç‡¶Ø ‡¶ó‡ßÅ‡¶∞‡ßÅ‡¶§‡ßç‡¶¨‡¶™‡ßÇ‡¶∞‡ßç‡¶£‡•§\"\r\nkeywords:\r\n  [\r\n    \"the man who said that\",\r\n    \"who vs whom\",\r\n    \"subject relative pronoun\",\r\n    \"english grammar correction\",\r\n    \"bcs english\",\r\n    \"correct sentence\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Choose the correct sentence ‚Äì\r\n\r\n**Question:** Choose the correct sentence ‚Äì\r\n(a) The man that said was a fool.\r\n(b) The man whom said that was a fool.\r\n(c) The man who said that was a fool.\r\n(d) The man which said was a fool.\r\n\r\n**Correct Answer:** (c) The man who said that was a fool.\r\n\r\n### ‡¶ï‡ßá‡¶® 'who' ‡¶∏‡¶†‡¶ø‡¶ï ‡¶â‡¶§‡ßç‡¶§‡¶∞? (Why is 'who' correct?)\r\n\r\n‡¶è‡¶á ‡¶™‡ßç‡¶∞‡¶∂‡ßç‡¶®‡ßá‡¶∞ ‡¶â‡¶§‡ßç‡¶§‡¶∞‡¶ü‡¶ø **Relative Pronoun** `who` ‡¶è‡¶¨‡¶Ç `whom`-‡¶è‡¶∞ ‡¶∏‡¶†‡¶ø‡¶ï ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞‡ßá‡¶∞ ‡¶â‡¶™‡¶∞ ‡¶®‡¶ø‡¶∞‡ßç‡¶≠‡¶∞‡¶∂‡ßÄ‡¶≤‡•§\r\n\r\n**Key Concept: Subject vs. Object Relative Pronoun**\r\n\r\n1.  **Antecedent:** Relative Pronoun-‡¶ü‡¶ø‡¶∞ ‡¶™‡ßÇ‡¶∞‡ßç‡¶¨‡¶¨‡¶∞‡ßç‡¶§‡ßÄ Noun ‡¶π‡¶≤‡ßã `The man`, ‡¶Ø‡¶æ ‡¶è‡¶ï‡¶ú‡¶® ‡¶¨‡ßç‡¶Ø‡¶ï‡ßç‡¶§‡¶ø‡•§ ‡¶¨‡ßç‡¶Ø‡¶ï‡ßç‡¶§‡¶ø‡¶∞ ‡¶ú‡¶®‡ßç‡¶Ø ‡¶Ü‡¶Æ‡¶∞‡¶æ `who`, `whom`, ‡¶¨‡¶æ `that` ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞ ‡¶ï‡¶∞‡¶§‡ßá ‡¶™‡¶æ‡¶∞‡¶ø‡•§\r\n\r\n2.  **`who` (Subject Pronoun):** ‡¶Ø‡¶ñ‡¶® Relative Pronoun-‡¶ü‡¶ø ‡¶≠‡ßá‡¶§‡¶∞‡ßá‡¶∞ clause-‡¶è‡¶∞ **subject** (‡¶ï‡¶∞‡ßç‡¶§‡¶æ) ‡¶π‡¶ø‡¶∏‡ßá‡¶¨‡ßá ‡¶ï‡¶æ‡¶ú ‡¶ï‡¶∞‡ßá, ‡¶§‡¶ñ‡¶® `who` ‡¶¨‡¶∏‡ßá‡•§\r\n3.  **`whom` (Object Pronoun):** ‡¶Ø‡¶ñ‡¶® Relative Pronoun-‡¶ü‡¶ø ‡¶≠‡ßá‡¶§‡¶∞‡ßá‡¶∞ clause-‡¶è‡¶∞ **object** (‡¶ï‡¶∞‡ßç‡¶Æ) ‡¶π‡¶ø‡¶∏‡ßá‡¶¨‡ßá ‡¶ï‡¶æ‡¶ú ‡¶ï‡¶∞‡ßá, ‡¶§‡¶ñ‡¶® `whom` ‡¶¨‡¶∏‡ßá‡•§\r\n\r\n**‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø ‡¶¨‡¶ø‡¶∂‡ßç‡¶≤‡ßá‡¶∑‡¶£:**\r\n\r\n- **Main Clause:** \"The man ... was a fool.\"\r\n- **Relative Clause:** \"\\_\\_\\_\\_ said that\"\r\n\r\n‡¶è‡¶á Relative Clause-‡¶ü‡¶ø‡¶§‡ßá `said` (‡¶¨‡¶≤‡ßá‡¶õ‡¶ø‡¶≤) verb-‡¶ü‡¶ø‡¶∞ ‡¶ï‡¶æ‡¶ú ‡¶ï‡ßá ‡¶ï‡¶∞‡ßá‡¶õ‡ßá? ‡¶Ö‡¶∞‡ßç‡¶•‡¶æ‡ßé, `said` verb-‡¶ü‡¶ø‡¶∞ subject ‡¶ï‡ßá? ‡¶â‡¶§‡ßç‡¶§‡¶∞‡¶ü‡¶ø ‡¶π‡¶≤‡ßã `The man`‡•§ ‡¶Ø‡ßá‡¶π‡ßá‡¶§‡ßÅ Relative Pronoun-‡¶ü‡¶ø `The man`-‡¶è‡¶∞ ‡¶™‡¶∞‡¶ø‡¶¨‡¶∞‡ßç‡¶§‡ßá clause-‡¶ü‡¶ø‡¶∞ subject ‡¶π‡¶ø‡¶∏‡ßá‡¶¨‡ßá ‡¶¨‡¶∏‡¶õ‡ßá, ‡¶§‡¶æ‡¶á ‡¶Ü‡¶Æ‡¶æ‡¶¶‡ßá‡¶∞ Subject Pronoun `who` ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞ ‡¶ï‡¶∞‡¶§‡ßá ‡¶π‡¶¨‡ßá‡•§\r\n\r\n**Structure:** The man (`subject` -> `who`) said that...\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** Relative Pronoun-‡¶è‡¶∞ ‡¶™‡¶∞‡ßá ‡¶Ø‡¶¶‡¶ø ‡¶∏‡¶∞‡¶æ‡¶∏‡¶∞‡¶ø ‡¶è‡¶ï‡¶ü‡¶ø **verb** (‡¶Ø‡ßá‡¶Æ‡¶®:\r\n  said, is, went, has) ‡¶•‡¶æ‡¶ï‡ßá, ‡¶§‡¶æ‡¶π‡¶≤‡ßá `who` ‡¶¨‡¶∏‡ßá‡•§ ‡¶Ü‡¶∞ ‡¶Ø‡¶¶‡¶ø pronoun-‡¶è‡¶∞ ‡¶™‡¶∞‡ßá ‡¶è‡¶ï‡¶ü‡¶ø\r\n  **subject** (‡¶Ø‡ßá‡¶Æ‡¶®: you, he, they) ‡¶•‡¶æ‡¶ï‡ßá, ‡¶§‡¶æ‡¶π‡¶≤‡ßá `whom` ‡¶¨‡¶∏‡ßá‡•§ - `...who\r\n  **said**...` (verb `said` ‡¶Ü‡¶õ‡ßá, ‡¶§‡¶æ‡¶á `who`) - `...whom **you** met...` (subject\r\n  `you` ‡¶Ü‡¶õ‡ßá, ‡¶§‡¶æ‡¶á `whom`)\r\n</Callout>\r\n\r\n### ‡¶≠‡ßÅ‡¶≤ ‡¶â‡¶§‡ßç‡¶§‡¶∞‡¶ó‡ßÅ‡¶≤‡ßã ‡¶ï‡ßá‡¶® ‡¶≠‡ßÅ‡¶≤?\r\n\r\n- **(a) The man that said was a fool:** ‡¶Ø‡¶¶‡¶ø‡¶ì `that` ‡¶¨‡ßç‡¶Ø‡¶ï‡ßç‡¶§‡¶ø‡¶∞ ‡¶™‡¶∞‡¶ø‡¶¨‡¶∞‡ßç‡¶§‡ßá ‡¶¨‡¶∏‡¶§‡ßá ‡¶™‡¶æ‡¶∞‡ßá, ‡¶ï‡¶ø‡¶®‡ßç‡¶§‡ßÅ `that`-‡¶è‡¶∞ ‡¶™‡¶∞‡ßá `that` (The man that said **that**...) ‡¶™‡ßÅ‡¶®‡¶∞‡¶æ‡¶¨‡ßÉ‡¶§‡ßç‡¶§‡¶ø‡¶Æ‡ßÇ‡¶≤‡¶ï ‡¶∂‡ßã‡¶®‡¶æ‡ßü ‡¶è‡¶¨‡¶Ç `who` ‡¶è‡¶ñ‡¶æ‡¶®‡ßá ÿßŸÑÿ£ŸÉÿ´ÿ± ‡¶∏‡ßç‡¶¨‡¶æ‡¶≠‡¶æ‡¶¨‡¶ø‡¶ï ‡¶è‡¶¨‡¶Ç ‡¶™‡¶õ‡¶®‡ßç‡¶¶‡ßá‡¶∞ ‡¶¨‡¶ø‡¶ï‡¶≤‡ßç‡¶™‡•§ ‡¶§‡¶¨‡ßá ‡¶ï‡¶ø‡¶õ‡ßÅ ‡¶ï‡ßç‡¶∑‡ßá‡¶§‡ßç‡¶∞‡ßá ‡¶è‡¶ü‡¶ø ‡¶Ö‡¶∂‡ßÅ‡¶¶‡ßç‡¶ß ‡¶®‡ßü, ‡¶ï‡¶ø‡¶®‡ßç‡¶§‡ßÅ `who` ‡¶¨‡ßá‡¶∂‡¶ø ‡¶∏‡¶†‡¶ø‡¶ï‡•§ _Note: The man that said that..._ is awkward. The question implies \"The man who said [the thing] was a fool\". The first 'that' is the relative pronoun, the second is the object. So, \"The man that said that was a fool\" is technically possible but less common than using 'who'. However, the option given is \"The man that said was a fool\", which is missing an object for 'said', making it incomplete.\r\n\r\n- **(b) The man whom said that was a fool:** `whom` ‡¶è‡¶ï‡¶ü‡¶ø Object Pronoun‡•§ ‡¶è‡¶ü‡¶ø verb-‡¶è‡¶∞ subject ‡¶π‡¶ø‡¶∏‡ßá‡¶¨‡ßá ‡¶¨‡¶∏‡¶§‡ßá ‡¶™‡¶æ‡¶∞‡ßá ‡¶®‡¶æ‡•§ ‡¶§‡¶æ‡¶á ‡¶è‡¶ü‡¶ø ‡¶≠‡ßÅ‡¶≤‡•§\r\n\r\n- **(d) The man which said was a fool:** `which` ‡¶∂‡ßÅ‡¶ß‡ßÅ‡¶Æ‡¶æ‡¶§‡ßç‡¶∞ ‡¶¨‡¶∏‡ßç‡¶§‡ßÅ ‡¶¨‡¶æ ‡¶á‡¶§‡¶∞ ‡¶™‡ßç‡¶∞‡¶æ‡¶£‡ßÄ‡¶∞ ‡¶ú‡¶®‡ßç‡¶Ø ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡ßÉ‡¶§ ‡¶π‡ßü‡•§ `The man` ‡¶¨‡ßç‡¶Ø‡¶ï‡ßç‡¶§‡¶ø ‡¶π‡¶ì‡ßü‡¶æ‡ßü `which` ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞ ‡¶ï‡¶∞‡¶æ ‡¶Ø‡¶æ‡¶¨‡ßá ‡¶®‡¶æ‡•§\r\n\r\n‡¶Ö‡¶§‡¶è‡¶¨, `The man who said that was a fool` ‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø‡¶ü‡¶ø‡¶á ‡¶¨‡ßç‡¶Ø‡¶æ‡¶ï‡¶∞‡¶£‡¶ó‡¶§‡¶≠‡¶æ‡¶¨‡ßá ‡¶∏‡¶Æ‡ßç‡¶™‡ßÇ‡¶∞‡ßç‡¶£ ‡¶∂‡ßÅ‡¶¶‡ßç‡¶ß‡•§";
