export const sourceCode = "---\r\ntitle: \"Interrogative Pronoun 'who' ‡¶è‡¶∞ ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞: Tell me who told you that\"\r\ndescription: \"Tell me ____ that - ‡¶∂‡ßÇ‡¶®‡ßç‡¶Ø‡¶∏‡ßç‡¶•‡¶æ‡¶®‡ßá whom told you, that told you, who told you, told you ‡¶ï‡ßã‡¶®‡¶ü‡¶ø ‡¶¨‡¶∏‡¶¨‡ßá? Interrogative Pronoun 'who' ‡¶è‡¶∞ ‡¶∏‡¶†‡¶ø‡¶ï ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞ ‡¶∂‡¶ø‡¶ñ‡ßÅ‡¶®‡•§\"\r\nkeywords:\r\n  [\r\n    \"interrogative pronoun who\",\r\n    \"who vs whom\",\r\n    \"embedded question\",\r\n    \"english grammar mcq\",\r\n    \"bcs english\",\r\n    \"tell me who told you\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Tell me \\_\\_\\_\\_ that.\r\n\r\n**Question:** Tell me \\_\\_\\_\\_ that.\r\n(a) whom told you\r\n(b) that told you\r\n(c) who told you\r\n(d) told you\r\n\r\n**Correct Answer:** (c) who told you\r\n\r\n### ‡¶ï‡ßá‡¶® 'who told you' ‡¶∏‡¶†‡¶ø‡¶ï ‡¶â‡¶§‡ßç‡¶§‡¶∞? (Why is 'who told you' correct?)\r\n\r\n‡¶è‡¶á ‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø‡¶ü‡¶ø ‡¶è‡¶ï‡¶ü‡¶ø **Embedded Question** (‡¶è‡¶ï‡¶ü‡¶ø ‡¶¨‡ßú ‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø‡ßá‡¶∞ ‡¶≠‡ßá‡¶§‡¶∞‡ßá ‡¶•‡¶æ‡¶ï‡¶æ ‡¶™‡ßç‡¶∞‡¶∂‡ßç‡¶®) ‡¶è‡¶∞ ‡¶â‡¶¶‡¶æ‡¶π‡¶∞‡¶£‡•§ ‡¶è‡¶∞ ‡¶∏‡¶†‡¶ø‡¶ï ‡¶ó‡¶†‡¶® ‡¶¨‡ßÅ‡¶ù‡¶§‡ßá ‡¶π‡¶≤‡ßá ‡¶Ü‡¶Æ‡¶æ‡¶¶‡ßá‡¶∞ **Interrogative Pronoun** `who` ‡¶è‡¶¨‡¶Ç `whom`-‡¶è‡¶∞ ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞ ‡¶ú‡¶æ‡¶®‡¶§‡ßá ‡¶π‡¶¨‡ßá‡•§\r\n\r\n**Key Concept: Subject vs. Object Pronoun in Questions**\r\n\r\n1.  **Who (Subject Pronoun):** ‡¶Ø‡¶ñ‡¶® ‡¶™‡ßç‡¶∞‡¶∂‡ßç‡¶®‡¶ü‡¶ø ‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø‡ßá‡¶∞ **Subject** (‡¶ï‡¶∞‡ßç‡¶§‡¶æ) ‡¶∏‡¶Æ‡ßç‡¶™‡¶∞‡ßç‡¶ï‡ßá ‡¶ï‡¶∞‡¶æ ‡¶π‡ßü, ‡¶§‡¶ñ‡¶® `who` ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡ßÉ‡¶§ ‡¶π‡ßü‡•§ `Who` ‡¶ú‡¶æ‡¶®‡¶§‡ßá ‡¶ö‡¶æ‡ßü '‡¶ï‡ßá' ‡¶ï‡¶æ‡¶ú‡¶ü‡¶ø ‡¶ï‡¶∞‡ßá‡¶õ‡ßá‡•§\r\n\r\n    - _Example:_ **Who** broke the window? (Subject `Who` + Verb `broke`)\r\n\r\n2.  **Whom (Object Pronoun):** ‡¶Ø‡¶ñ‡¶® ‡¶™‡ßç‡¶∞‡¶∂‡ßç‡¶®‡¶ü‡¶ø ‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø‡ßá‡¶∞ **Object** (‡¶ï‡¶∞‡ßç‡¶Æ) ‡¶∏‡¶Æ‡ßç‡¶™‡¶∞‡ßç‡¶ï‡ßá ‡¶ï‡¶∞‡¶æ ‡¶π‡ßü, ‡¶§‡¶ñ‡¶® `whom` ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡ßÉ‡¶§ ‡¶π‡ßü‡•§ `Whom` ‡¶ú‡¶æ‡¶®‡¶§‡ßá ‡¶ö‡¶æ‡ßü ‡¶ï‡¶æ‡¶ú‡¶ü‡¶ø '‡¶ï‡¶æ‡¶ï‡ßá' ‡¶ï‡¶∞‡¶æ ‡¶π‡ßü‡ßá‡¶õ‡ßá‡•§\r\n    - _Example:_ **Whom** did you see? (Object `Whom` + Auxiliary `did` + Subject `you` + Verb `see`)\r\n\r\n**‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø ‡¶¨‡¶ø‡¶∂‡ßç‡¶≤‡ßá‡¶∑‡¶£:**\r\n\r\n- Main Clause: \"Tell me...\" (‡¶Ü‡¶Æ‡¶æ‡¶ï‡ßá ‡¶¨‡¶≤‡ßã...)\r\n- Embedded Question: \"...\\_\\_\\_\\_ told you that.\" (...‡¶ï‡ßá ‡¶§‡ßã‡¶Æ‡¶æ‡¶ï‡ßá ‡¶ì‡¶ü‡¶æ ‡¶¨‡¶≤‡ßá‡¶õ‡ßá‡•§)\r\n\r\n‡¶è‡¶á ‡¶≠‡ßá‡¶§‡¶∞‡ßá‡¶∞ ‡¶Ö‡¶Ç‡¶∂‡ßá, `told` verb-‡¶ü‡¶ø‡¶∞ ‡¶ï‡¶æ‡¶ú ‡¶ï‡ßá ‡¶ï‡¶∞‡ßá‡¶õ‡ßá? ‡¶Ö‡¶∞‡ßç‡¶•‡¶æ‡ßé, `told`-‡¶è‡¶∞ subject ‡¶ï‡ßá? ‡¶â‡¶§‡ßç‡¶§‡¶∞‡¶ü‡¶ø ‡¶π‡¶≤‡ßã '‡¶ï‡ßá' ‡¶¨‡¶æ `who`‡•§\r\n\r\n- **Subject:** `who`\r\n- **Verb:** `told`\r\n- **Indirect Object:** `you`\r\n- **Direct Object:** `that`\r\n\r\n‡¶Ø‡ßá‡¶π‡ßá‡¶§‡ßÅ ‡¶Ü‡¶Æ‡¶æ‡¶¶‡ßá‡¶∞ `told` verb-‡¶ü‡¶ø‡¶∞ subject ‡¶™‡ßç‡¶∞‡ßü‡ßã‡¶ú‡¶®, ‡¶§‡¶æ‡¶á `who` ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞ ‡¶ï‡¶∞‡¶§‡ßá ‡¶π‡¶¨‡ßá‡•§\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** ‡¶â‡¶§‡ßç‡¶§‡¶∞‡¶ü‡¶ø `He` ‡¶¨‡¶æ `She` ‡¶¶‡¶ø‡ßü‡ßá ‡¶¶‡ßá‡¶ì‡ßü‡¶æ ‡¶ó‡ßá‡¶≤‡ßá ‡¶™‡ßç‡¶∞‡¶∂‡ßç‡¶® `Who` ‡¶¶‡¶ø‡ßü‡ßá\r\n  ‡¶π‡¶¨‡ßá‡•§ ‡¶â‡¶§‡ßç‡¶§‡¶∞‡¶ü‡¶ø `Him` ‡¶¨‡¶æ `Her` ‡¶¶‡¶ø‡ßü‡ßá ‡¶¶‡ßá‡¶ì‡ßü‡¶æ ‡¶ó‡ßá‡¶≤‡ßá ‡¶™‡ßç‡¶∞‡¶∂‡ßç‡¶® `Whom` ‡¶¶‡¶ø‡ßü‡ßá ‡¶π‡¶¨‡ßá‡•§ -\r\n  **Question:** Who told you that? - **Possible Answer:** **He** told me that.\r\n  (‡¶â‡¶§‡ßç‡¶§‡¶∞ `He` (subject), ‡¶§‡¶æ‡¶á ‡¶™‡ßç‡¶∞‡¶∂‡ßç‡¶® `Who` ‡¶¶‡¶ø‡ßü‡ßá)\r\n</Callout>\r\n\r\n### ‡¶≠‡ßÅ‡¶≤ ‡¶â‡¶§‡ßç‡¶§‡¶∞‡¶ó‡ßÅ‡¶≤‡ßã ‡¶ï‡ßá‡¶® ‡¶≠‡ßÅ‡¶≤?\r\n\r\n- **(a) whom told you:** `whom` ‡¶è‡¶ï‡¶ü‡¶ø object pronoun‡•§ ‡¶è‡¶ü‡¶ø verb-‡¶è‡¶∞ subject ‡¶π‡¶ø‡¶∏‡ßá‡¶¨‡ßá ‡¶¨‡¶∏‡¶§‡ßá ‡¶™‡¶æ‡¶∞‡ßá ‡¶®‡¶æ‡•§ `Whom` ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞ ‡¶ï‡¶∞‡¶≤‡ßá ‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø‡¶ü‡¶ø ‡¶π‡¶§‡ßã: \"Tell me **whom** you told.\" (‡¶§‡ßÅ‡¶Æ‡¶ø ‡¶ï‡¶æ‡¶ï‡ßá ‡¶¨‡¶≤‡ßá‡¶õ‡¶ø‡¶≤‡ßá)‡•§\r\n- **(b) that told you:** `that` ‡¶è‡¶ñ‡¶æ‡¶®‡ßá Interrogative Pronoun ‡¶π‡¶ø‡¶∏‡ßá‡¶¨‡ßá ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡ßÉ‡¶§ ‡¶π‡¶§‡ßá ‡¶™‡¶æ‡¶∞‡ßá ‡¶®‡¶æ‡•§ `that` ‡¶∏‡¶æ‡¶ß‡¶æ‡¶∞‡¶£‡¶§ Relative Pronoun ‡¶π‡¶ø‡¶∏‡ßá‡¶¨‡ßá ‡¶¨‡¶∏‡ßá‡•§\r\n- **(d) told you:** ‡¶è‡¶ñ‡¶æ‡¶®‡ßá ‡¶ï‡ßã‡¶®‡ßã subject ‡¶®‡ßá‡¶á, ‡¶Ø‡¶æ ‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø‡¶ü‡¶ø‡¶ï‡ßá ‡¶Ö‡¶∏‡¶Æ‡ßç‡¶™‡ßÇ‡¶∞‡ßç‡¶£ ‡¶ï‡¶∞‡ßá ‡¶¶‡¶ø‡ßü‡ßá‡¶õ‡ßá‡•§ \"Tell me...\" ‡¶è‡¶∞ ‡¶™‡¶∞ ‡¶è‡¶ï‡¶ü‡¶ø ‡¶∏‡¶Æ‡ßç‡¶™‡ßÇ‡¶∞‡ßç‡¶£ clause ‡¶™‡ßç‡¶∞‡ßü‡ßã‡¶ú‡¶®‡•§\r\n\r\n\"Tell me who told you that\" ‡¶è‡¶ï‡¶ü‡¶ø ‡¶∏‡¶Æ‡ßç‡¶™‡ßÇ‡¶∞‡ßç‡¶£ ‡¶è‡¶¨‡¶Ç ‡¶¨‡ßç‡¶Ø‡¶æ‡¶ï‡¶∞‡¶£‡¶ó‡¶§‡¶≠‡¶æ‡¶¨‡ßá ‡¶∂‡ßÅ‡¶¶‡ßç‡¶ß ‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø, ‡¶Ø‡¶æ‡¶∞ ‡¶Ö‡¶∞‡ßç‡¶• \"‡¶Ü‡¶Æ‡¶æ‡¶ï‡ßá ‡¶¨‡¶≤‡ßã ‡¶ï‡ßá ‡¶§‡ßã‡¶Æ‡¶æ‡¶ï‡ßá ‡¶ì‡¶ü‡¶æ ‡¶¨‡¶≤‡ßá‡¶õ‡ßá‡•§\"";
