export const sourceCode = "---\r\ntitle: \"Correct Sentence: 'in hospital' vs 'in a hospital', 'for' vs 'during'\"\r\ndescription: \"My father was in hospital for six weeks during the summer - ‡¶ï‡ßá‡¶® ‡¶è‡¶á ‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø‡¶ü‡¶ø ‡¶∂‡ßÅ‡¶¶‡ßç‡¶ß? Article ‡¶è‡¶¨‡¶Ç Preposition of Time ‡¶è‡¶∞ ‡¶∏‡¶†‡¶ø‡¶ï ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞ ‡¶∂‡¶ø‡¶ñ‡ßÅ‡¶®‡•§ BCS, Primary ‡¶™‡¶∞‡ßÄ‡¶ï‡ßç‡¶∑‡¶æ‡¶∞ ‡¶ú‡¶®‡ßç‡¶Ø ‡¶ó‡ßÅ‡¶∞‡ßÅ‡¶§‡ßç‡¶¨‡¶™‡ßÇ‡¶∞‡ßç‡¶£‡•§\"\r\nkeywords:\r\n  [\r\n    \"in hospital vs in a hospital\",\r\n    \"for vs during\",\r\n    \"preposition of time\",\r\n    \"english grammar correction\",\r\n    \"bcs english\",\r\n    \"correct sentence structure\",\r\n  ]\r\n---\r\n\r\nimport { Callout } from \"nextra/components\";\r\n\r\n## MCQ Analysis: Which of the following sentences is correct one?\r\n\r\n**Question:** Which of the following sentences is correct one?\r\n(a) My father was in hospital during six weeks in summer.\r\n(b) In summer during six weeks my father was in hospital.\r\n(c) My father was in a hospital during six weeks in summer.\r\n(d) My father was in hospital for six weeks during the summer.\r\n\r\n**Correct Answer:** (d) My father was in hospital for six weeks during the summer.\r\n\r\n### ‡¶ï‡ßá‡¶® (d) ‡¶∏‡¶†‡¶ø‡¶ï ‡¶â‡¶§‡ßç‡¶§‡¶∞? (Why is (d) correct?)\r\n\r\n‡¶è‡¶á ‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø‡¶ü‡¶ø‡¶∞ ‡¶∂‡ßÅ‡¶¶‡ßç‡¶ß‡¶§‡¶æ ‡¶¶‡ßÅ‡¶ü‡¶ø ‡¶™‡ßç‡¶∞‡¶ß‡¶æ‡¶® ‡¶¨‡¶ø‡¶∑‡¶Ø‡¶º‡ßá‡¶∞ ‡¶â‡¶™‡¶∞ ‡¶®‡¶ø‡¶∞‡ßç‡¶≠‡¶∞‡¶∂‡ßÄ‡¶≤:\r\n\r\n1.  **Article-‡¶è‡¶∞ ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞:** `in hospital` ‡¶¨‡¶®‡¶æ‡¶Æ `in a hospital`‡•§\r\n2.  **Preposition of Time-‡¶è‡¶∞ ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞:** `for` ‡¶¨‡¶®‡¶æ‡¶Æ `during`‡•§\r\n\r\n---\r\n\r\n### 1. Article Usage: `in hospital` vs. `in a/the hospital`\r\n\r\n**Key Concept:** ‡¶Ø‡¶ñ‡¶® `hospital`, `school`, `college`, `church`, `prison` ‡¶á‡¶§‡ßç‡¶Ø‡¶æ‡¶¶‡¶ø ‡¶∏‡ßç‡¶•‡¶æ‡¶® ‡¶§‡¶æ‡¶¶‡ßá‡¶∞ ‡¶Æ‡ßÇ‡¶≤ ‡¶â‡¶¶‡ßç‡¶¶‡ßá‡¶∂‡ßç‡¶Ø‡ßá (e.g., ‡¶π‡¶æ‡¶∏‡¶™‡¶æ‡¶§‡¶æ‡¶≤‡ßá ‡¶ö‡¶ø‡¶ï‡¶ø‡ßé‡¶∏‡¶æ‡¶∞ ‡¶ú‡¶®‡ßç‡¶Ø, ‡¶∏‡ßç‡¶ï‡ßÅ‡¶≤‡ßá ‡¶™‡ßú‡¶æ‡¶∞ ‡¶ú‡¶®‡ßç‡¶Ø) ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡ßÉ‡¶§ ‡¶π‡ßü, ‡¶§‡¶ñ‡¶® British English-‡¶è ‡¶è‡¶¶‡ßá‡¶∞ ‡¶Ü‡¶ó‡ßá ‡¶ï‡ßã‡¶®‡ßã Article (a, an, the) ‡¶¨‡¶∏‡ßá ‡¶®‡¶æ‡•§ American English-‡¶è `the` ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡ßÉ‡¶§ ‡¶π‡ßü (`in the hospital`)‡•§\r\n\r\n- **in hospital (BrE) / in the hospital (AmE):** ‡¶∞‡ßã‡¶ó‡ßÄ ‡¶π‡¶ø‡¶∏‡ßá‡¶¨‡ßá ‡¶π‡¶æ‡¶∏‡¶™‡¶æ‡¶§‡¶æ‡¶≤‡ßá ‡¶≠‡¶∞‡ßç‡¶§‡¶ø ‡¶•‡¶æ‡¶ï‡¶æ‡•§\r\n  - _Example:_ He is **in hospital** with a broken leg.\r\n- **in a/the hospital:** ‡¶ï‡ßã‡¶®‡ßã ‡¶¶‡¶∞‡ßç‡¶∂‡¶®‡¶æ‡¶∞‡ßç‡¶•‡ßÄ ‡¶π‡¶ø‡¶∏‡ßá‡¶¨‡ßá ‡¶¨‡¶æ ‡¶Ö‡¶®‡ßç‡¶Ø ‡¶ï‡ßã‡¶®‡ßã ‡¶ï‡¶æ‡¶ú‡ßá ‡¶π‡¶æ‡¶∏‡¶™‡¶æ‡¶§‡¶æ‡¶≤‡ßá‡¶∞ ‡¶¨‡¶ø‡¶≤‡ßç‡¶°‡¶ø‡¶Ç‡ßü‡ßá‡¶∞ ‡¶≠‡ßá‡¶§‡¶∞‡ßá ‡¶•‡¶æ‡¶ï‡¶æ‡•§\r\n  - _Example:_ I went to **the hospital** to visit my friend.\r\n\r\n‡¶Ø‡ßá‡¶π‡ßá‡¶§‡ßÅ ‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø‡ßá ‡¶¨‡¶æ‡¶¨‡¶æ ‡¶Ö‡¶∏‡ßÅ‡¶∏‡ßç‡¶• ‡¶π‡ßü‡ßá ‡¶π‡¶æ‡¶∏‡¶™‡¶æ‡¶§‡¶æ‡¶≤‡ßá ‡¶õ‡¶ø‡¶≤‡ßá‡¶® ‡¶¨‡ßã‡¶ù‡¶æ‡¶ö‡ßç‡¶õ‡ßá, ‡¶§‡¶æ‡¶á `in hospital` (BrE style) ‡¶¨‡¶æ `in the hospital` (AmE style) ‡¶∏‡¶†‡¶ø‡¶ï‡•§ Option (c)-‡¶§‡ßá `in a hospital` ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞‡¶ü‡¶ø ‡¶è‡¶ñ‡¶æ‡¶®‡ßá ‡¶≠‡ßÅ‡¶≤, ‡¶ï‡¶æ‡¶∞‡¶£ ‡¶è‡¶ü‡¶ø ‡¶®‡¶ø‡¶∞‡ßç‡¶¶‡¶ø‡¶∑‡ßç‡¶ü ‡¶®‡ßü ‡¶è‡¶¨‡¶Ç ‡¶∞‡ßã‡¶ó‡ßÄ ‡¶π‡¶ø‡¶∏‡ßá‡¶¨‡ßá ‡¶•‡¶æ‡¶ï‡¶æ‡¶ï‡ßá ‡¶¨‡ßã‡¶ù‡¶æ‡¶ö‡ßç‡¶õ‡ßá ‡¶®‡¶æ‡•§\r\n\r\n---\r\n\r\n### 2. Preposition of Time: `for` vs. `during`\r\n\r\n**Key Concept:**\r\n\r\n- **For:** ‡¶è‡¶ï‡¶ü‡¶ø ‡¶®‡¶ø‡¶∞‡ßç‡¶¶‡¶ø‡¶∑‡ßç‡¶ü ‡¶∏‡¶Æ‡ßü‡¶ï‡¶æ‡¶≤ ‡¶¨‡¶æ ÿßŸÑŸÖÿØÿ© (period of time) ‡¶¨‡ßã‡¶ù‡¶æ‡¶§‡ßá ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡ßÉ‡¶§ ‡¶π‡ßü‡•§ ‡¶Ø‡ßá‡¶Æ‡¶®: `for three days`, `for two years`, `for six weeks`‡•§\r\n- **During:** ‡¶è‡¶ï‡¶ü‡¶ø ‡¶®‡¶ø‡¶∞‡ßç‡¶¶‡¶ø‡¶∑‡ßç‡¶ü ‡¶ò‡¶ü‡¶®‡¶æ‡¶∞ ‡¶¨‡¶æ ‡¶∏‡¶Æ‡ßü‡¶ï‡¶æ‡¶≤‡ßá‡¶∞ ‡¶Æ‡¶ß‡ßç‡¶Ø‡ßá '‡¶ï‡¶ñ‡¶®' ‡¶ï‡¶ø‡¶õ‡ßÅ ‡¶ò‡¶ü‡ßá‡¶õ‡¶ø‡¶≤ ‡¶§‡¶æ ‡¶¨‡ßã‡¶ù‡¶æ‡¶§‡ßá ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡ßÉ‡¶§ ‡¶π‡ßü‡•§ ‡¶è‡¶∞ ‡¶™‡¶∞ ‡¶∏‡¶æ‡¶ß‡¶æ‡¶∞‡¶£‡¶§ ‡¶è‡¶ï‡¶ü‡¶ø Noun ‡¶•‡¶æ‡¶ï‡ßá‡•§ ‡¶Ø‡ßá‡¶Æ‡¶®: `during the meeting`, `during the night`, `during the summer`‡•§\r\n\r\n**‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø ‡¶¨‡¶ø‡¶∂‡ßç‡¶≤‡ßá‡¶∑‡¶£:**\r\n\r\n- \"six weeks\" ‡¶è‡¶ï‡¶ü‡¶ø ‡¶∏‡¶Æ‡ßü‡¶ï‡¶æ‡¶≤ (period)‡•§ ‡¶§‡¶æ‡¶á ‡¶è‡¶∞ ‡¶Ü‡¶ó‡ßá `for` ‡¶¨‡¶∏‡¶¨‡ßá‡•§ `during six weeks` ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞‡¶ü‡¶ø ‡¶≠‡ßÅ‡¶≤‡•§\r\n- \"the summer\" ‡¶è‡¶ï‡¶ü‡¶ø ‡¶®‡¶ø‡¶∞‡ßç‡¶¶‡¶ø‡¶∑‡ßç‡¶ü ‡¶∏‡¶Æ‡ßü‡¶ï‡¶æ‡¶≤ ‡¶¨‡¶æ ‡¶ò‡¶ü‡¶®‡¶æ‡•§ ‡¶è‡¶∞ ‡¶Æ‡¶ß‡ßç‡¶Ø‡ßá ‡¶õ‡ßü ‡¶∏‡¶™‡ßç‡¶§‡¶æ‡¶π ‡¶§‡¶ø‡¶®‡¶ø ‡¶π‡¶æ‡¶∏‡¶™‡¶æ‡¶§‡¶æ‡¶≤‡ßá ‡¶õ‡¶ø‡¶≤‡ßá‡¶®‡•§ ‡¶§‡¶æ‡¶á `during the summer` ‡¶∏‡¶†‡¶ø‡¶ï‡•§\r\n\r\n**‡¶∏‡¶†‡¶ø‡¶ï ‡¶∏‡¶Æ‡¶®‡ßç‡¶¨‡ßü:**\r\n\r\n- ‡¶∏‡¶Æ‡ßü‡¶ï‡¶æ‡¶≤ ‡¶¨‡ßã‡¶ù‡¶æ‡¶§‡ßá: `for six weeks`\r\n- ‡¶ï‡¶ñ‡¶® ‡¶ò‡¶ü‡ßá‡¶õ‡¶ø‡¶≤ ‡¶¨‡ßã‡¶ù‡¶æ‡¶§‡ßá: `during the summer`\r\n\r\n<Callout type=\"info\">\r\n  **Hack to Remember:** - `for` + a period of time (e.g., for 2 hours, for 5\r\n  days). - `during` + a noun (e.g., during the film, during the holiday). - `in\r\n  hospital` = ‡¶∞‡ßã‡¶ó‡ßÄ ‡¶π‡¶ø‡¶∏‡ßá‡¶¨‡ßá ‡¶•‡¶æ‡¶ï‡¶æ‡•§\r\n</Callout>\r\n\r\n### ‡¶≠‡ßÅ‡¶≤ ‡¶â‡¶§‡ßç‡¶§‡¶∞‡¶ó‡ßÅ‡¶≤‡ßã ‡¶ï‡ßá‡¶® ‡¶≠‡ßÅ‡¶≤?\r\n\r\n- **(a) ...during six weeks...:** ‡¶∏‡¶Æ‡ßü‡¶ï‡¶æ‡¶≤‡ßá‡¶∞ ‡¶Ü‡¶ó‡ßá `during` ‡¶≠‡ßÅ‡¶≤‡•§ `for` ‡¶π‡¶¨‡ßá‡•§\r\n- **(b) In summer during six weeks...:** ‡¶è‡¶ñ‡¶æ‡¶®‡ßá‡¶ì `during six weeks` ‡¶≠‡ßÅ‡¶≤ ‡¶è‡¶¨‡¶Ç ‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø‡ßá‡¶∞ ‡¶ó‡¶†‡¶® ‡¶¶‡ßÅ‡¶∞‡ßç‡¶¨‡¶≤‡•§\r\n- **(c) ...in a hospital...:** `a hospital` ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡¶æ‡¶∞‡¶ü‡¶ø ‡¶è‡¶ñ‡¶æ‡¶®‡ßá ‡¶≠‡ßÅ‡¶≤, ‡¶ï‡¶æ‡¶∞‡¶£ ‡¶∞‡ßã‡¶ó‡ßÄ ‡¶π‡¶ø‡¶∏‡ßá‡¶¨‡ßá ‡¶•‡¶æ‡¶ï‡¶æ‡¶∞ ‡¶Ö‡¶∞‡ßç‡¶• ‡¶™‡ßç‡¶∞‡¶ï‡¶æ‡¶∂‡ßá `in hospital` ‡¶¨‡¶æ `in the hospital` ‡¶¨‡ßç‡¶Ø‡¶¨‡¶π‡ßÉ‡¶§ ‡¶π‡ßü‡•§\r\n\r\nOption (d) ‡¶è‡¶á ‡¶∏‡¶¨ ‡¶®‡¶ø‡ßü‡¶Æ ‡¶∏‡¶†‡¶ø‡¶ï‡¶≠‡¶æ‡¶¨‡ßá ‡¶Ö‡¶®‡ßÅ‡¶∏‡¶∞‡¶£ ‡¶ï‡¶∞‡ßá, ‡¶§‡¶æ‡¶á ‡¶è‡¶ü‡¶ø‡¶á ‡¶∂‡ßÅ‡¶¶‡ßç‡¶ß ‡¶¨‡¶æ‡¶ï‡ßç‡¶Ø‡•§";
